import React from "react";
import "../css/HeaderOptions.css";
function HeaderOption({ user = "Guest", isLogged = "false" }) {
  function signInStatus(e) {
    if (e.isLogged === "true") return "Account & Lists";
    return "Sign In";
  }
  return (
    <div className="headerOptions">
      <div className="option">
        <small>Hello, {user}</small>
        <strong>{signInStatus({ isLogged })}</strong>
      </div>
    </div>
  );
}

export default HeaderOption;
