import React from "react";
import "./App.css";
import Header from "./JS/Header";
import Home from "./JS/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import NavigationPanel from "./JS/Navigation";
import Prime from "./JS/Prime";
import DealOfTheDay from "./JS/DealOfTheDay";

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/Checkout">
            <Header />
            <NavigationPanel />
          </Route>
          <Route path="/">
            <Header />
            <NavigationPanel />
            <Home />
            <Prime />
            <DealOfTheDay />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
