import React from "react";
import "./stateProvider.css";

function stateProvider() {
  const store = [
    {
      id: 1,
      title: "First Product",
      imgSrc:
        "https://images.unsplash.com/photo-1546868871-7041f2a55e12?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTd8fHByb2R1Y3R8ZW58MHx8MHx8&w=1000&q=80",
      category: "Watches",
      count: 2,
    },
    {
      id: 3,
      title: "Second Product",
      imgSrc:
        "https://images-eu.ssl-images-amazon.com/images/G/31/AmazonVideo/2021/X-site/SingleTitle/Satyamevjayate2/Launch/400x39-SWM_JPN._CB649182577_.jpg",
      category: "Prime",
      count: 1,
    },
  ];
  var length = store.length;
  let items = 0;
  if (length > 0) {
    for (let element of store) {
      items += element.count;
    }
  }
  return <div className="itemCount">{items}</div>;
}

export default stateProvider;
