import React from "react";
import DealItems from "../database/dealOfTheDay.json";
import "../css/DealOfTheDay.css";
import rateImg from "../images/rate-img.png";
function DealOfTheDay() {
  return (
    <div className="deal">
      <div className="dealWrapper">
        <span className="title">Deal of the Day</span>

        <div className="deals">
          {DealItems.map((item, index) => {
            return (
              <div className="items">
                <div className="imgContainer">
                  <img src={item.imgSrc} />
                </div>
                <div className="itemDetails">
                  <span className="itemTitle">{item.title}</span>
                  <span className="itemRating">
                    {Array(item.rating)
                      .fill()
                      .map((_, i) => (
                        <img src={rateImg} />
                      ))}
                  </span>
                  <div className="addToCart">
                    <span className="itemPrice">Rs.{item.price}</span>
                    <button>Add to cart</button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

export default DealOfTheDay;
